<template>
	<div class="pl-5 pr-5">
		<!-- type01 -->
		<div>
			<h2>Type01</h2>
			<v-layout style="height: 130px;" class="border rounded mt-3">
				<div class="mx-auto my-4">
					<v-btn
						color="deep-purple"
						variant="outlined"
						@click="active = !active"
					>Toggle</v-btn>
				</div>

				<v-bottom-navigation
					:active="active"
					color="indigo"
				>
					<v-btn>
						<v-icon>mdi-history</v-icon>
						tit01
					</v-btn>
					<v-btn>
						<v-icon>mdi-heart</v-icon>
						tit02
					</v-btn>
					<v-btn>
						<v-icon>mdi-map-marker</v-icon>
						tit03
					</v-btn>
				</v-bottom-navigation>
			</v-layout>
		</div>

		<!-- type02 -->
		<div class="mt-5">
			<h2>Type02</h2>
			<v-card class="mt-3">
				<v-layout>
					<v-navigation-drawer
						expand-on-hover
						rail
					>
						<v-list>
							<v-list-item
								prepend-avatar="https://dummyimage.com/300x300/000/fff"
								title="name01"
								subtitle="subtitle01"
							></v-list-item>
						</v-list>

						<v-divider></v-divider>

						<v-list density="compact" nav>
							<v-list-item prepend-icon="mdi-folder" title="name001" value="val001"></v-list-item>
							<v-list-item prepend-icon="mdi-account-multiple" title="name002" value="val002"></v-list-item>
							<v-list-item prepend-icon="mdi-star" title="name003" value="val003"></v-list-item>
						</v-list>
					</v-navigation-drawer>

					<v-main style="height: 250px"></v-main>
				</v-layout>
			</v-card>
		</div>

		<!-- type03 -->
		<div class="mt-5">
			<h2>Type03</h2>
			<v-card class="mt-3">
				<v-layout>
					<v-navigation-drawer
						v-model="drawer"
						temporary
					>
						<v-list-item
							prepend-avatar="https://dummyimage.com/300x300/000/fff"
							title="name01"
						></v-list-item>

						<v-divider></v-divider>

						<v-list density="compact" nav>
							<v-list-item prepend-icon="mdi-view-dashboard" title="name001" value="001"></v-list-item>
							<v-list-item prepend-icon="mdi-forum" title="name002" value="002"></v-list-item>
						</v-list>
					</v-navigation-drawer>
					<v-main style="height: 250px">
						<div class="d-flex justify-center align-center h-100">
							<v-btn
								color="primary"
								@click.stop="drawer = !drawer"
							>
								Toggle
							</v-btn>
						</div>
					</v-main>
				</v-layout>
			</v-card>
		</div>

		<!-- type04 -->
		<div class="mt-5">
			<h2>Type04</h2>
			<v-pagination 
				class="mt-3"
				v-model="page1"
				:length="4"
				rounded="circle"
			></v-pagination>

			<v-pagination
				v-model="page2"
				:length="4"
				rounded="0"
			></v-pagination>
		</div>

		<!-- type05 -->
		<div class="mt-5 mb-10">
			<h2>Type05</h2>
			<v-card
				class="mt-3"
				color="black"
				max-width="500"
			>
				<v-toolbar flat color="primary">
				<v-btn icon="mdi-account"></v-btn>

				<v-toolbar-title class="font-weight-light">
					register ex
				</v-toolbar-title>

				<v-spacer></v-spacer>

				<v-btn
					icon
					@click="isEditing = !isEditing"
				>
					<v-fade-transition leave-absolute>
					<v-icon v-if="isEditing">mdi-close</v-icon>

					<v-icon v-else>mdi-pencil</v-icon>
					</v-fade-transition>
				</v-btn>
				</v-toolbar>

				<v-card-text>
				<v-text-field
					:disabled="!isEditing"
					base-color="white"
					label="Name"
				></v-text-field>

				<v-autocomplete
					:disabled="!isEditing"
					:items="states"
					:custom-filter="customFilter"
					base-color="white"
					item-title="name"
					item-value="abbr"
					label="State"
				></v-autocomplete>
				</v-card-text>

				<v-divider></v-divider>

				<v-card-actions>
				<v-spacer></v-spacer>

				<v-btn
					:disabled="!isEditing"
					@click="save"
				>
					Save
				</v-btn>
				</v-card-actions>

				<v-snackbar
				v-model="hasSaved"
				:timeout="2000"
				attach
				position="absolute"
				location="bottom left"
				>
					update done
				</v-snackbar>
			</v-card>
		</div>

		<!-- type06 -->
		<div class="mt-5 mb-10">
			<h2>Type06</h2>
			<canvas class="mt-3" style="border: 1px solid rgba(0, 0, 0, 0.2);" ref="signatureCanvas" v-on:mousedown="onBegin" v-on:mouseup="onEnd"></canvas>
			<div>
				<v-btn
					color="danger"
					@click="clearCanvas"
				>삭제</v-btn>
			</div>
		</div>
	</div>
</template>

<script>
	import SignaturePad from 'signature_pad';

	export default {
		// data: () => ({active: true})
		data () {
			return {
				active: true,
				drawer: null,
				page1: 1,
				page2: 1,
				hasSaved: false,
				isEditing: null,
				signaturePad: null,
				states: [
					{name:'001', abbr: 'fl', id:1},
					{name:'002', abbr: 'fl2', id:2},
					{name:'003', abbr: 'fl3', id:3},
					{name:'004', abbr: 'fl4', id:4},
					{name:'005', abbr: 'fl5', id:5},
				]
			}
		},
		mounted() {
			// const canvas = this.$refs.signatureCanvas;
			// this.signaturePad = new SignaturePad(canvas);

			this.$nextTick(() => {
				const canvas = this.$refs.signatureCanvas;
				this.signaturePad = new SignaturePad(canvas);
			})
		},
		methods: {
			customFilter (itemTitle, queryText, item) {
				const textOne = item.raw.name.toLowerCase()
				const textTwo = item.raw.abbr.toLowerCase()
				const searchText = queryText.toLowerCase()

				return textOne.indexOf(searchText) > -1 || textTwo.indexOf(searchText) > -1
			},
			save () {
				this.isEditing = !this.isEditing
				this.hasSaved = true
			},
			clearCanvas() {
				this.signaturePad.clear();
			},
			onBegin() {
				this.signaturePad.onBegin();
			},
			onEnd() {
				this.signaturePad.onEnd();
			}
		},
	}
</script>